<template>
  <div></div>
</template>

<script>
import tour from "../service";
export default {
  mounted() {
    this.Confirm();
  },
  data() {
    return {
      ConfirmData: {
        amount: this.$route.query.amount,
        currency: "TWD",
      },
    };
  },
  methods: {
    async Confirm() {
      const confirm_data = (
        await tour.confirm(this.$route.query, this.ConfirmData)
      ).data;
      alert(JSON.stringify(confirm_data));
    },
  },
};
</script>