import axios from 'axios'

export default () => {
    const headerToken = localStorage.getItem('headerToken')

    let createData = {}

    if (!headerToken) {
        createData = {
            baseURL: `http://localhost:5000/`
        }
    }
    else {
        createData = {
            baseURL: `http://localhost:5000/`,
            headers: { "TP-TOKEN": headerToken }
        }
    }
    return axios.create(createData)
}